<template>
  <div class="iphoneframe-container" ref="iphone">
    <router-link to="/account">
      <BackButton />
    </router-link>
    <transition name="fade-top">
      <PreviewCardWithIphoneFrame
        class="w-100"
        :image="previewCardImage"
        type="category"
        :title="previewCardTitle"
        :desc="previewCardDesc"
        :titleMyan="previewCardTitleMyan"
        :descMyan="previewCardDescMyan"
        :isFindOther="isFindOther"
        :isContactUs="isContactUs"
        firstButtonText="Find related products"
        secondButtonText="Find others"
        thirdButtonText="Contact us"
        v-show="showPreview"
      />
    </transition>
    <div class="mt-4 w-100 d-flex justify-content-center" v-if="showPreview">
      <button class="btn btn-circle" @click="showPreview = false">
        <CloseIcon style="width: 0.8rem;" />
      </button>
    </div>

    <div class="mt-4 w-100 title ml-5">
      <StageTitle stageNum="2" :title="stageInfoText.title" :desc="stageInfoText.desc" />
    </div>
    <div class="w-100">
      <div v-for="(catinfo, index) in categoryList" :key="index">
        <div v-show="selectedCategory == index">
          <div class="mt-4 p-3 frame">
            <div class="count d-flex flex-center">
              <span
                class="menu menu--arrow-up d-flex flex-center"
                v-if="index != 0"
                @click="selectCategory(index - 1)"
              >
                <UpArrowIcon class="arrow-icon" />
              </span>
              <span
                class="menu menu--arrow-down d-flex flex-center"
                v-if="index != categoryList.length - 1"
                @click="selectCategory(index + 1)"
              >
                <DownArrowIcon class="arrow-icon" />
              </span>
              <span>{{ index + 1 }}</span>
            </div>
            <AddedForm
              v-model="categoryList[index]"
              :info="info"
              type="category"
              :uploading="photoUploading && selectedCategory == index"
              @add="addCategory"
              @uploadPhoto="uploadPhoto"
            />
          </div>
          <div class="mt-2 w-100 d-flex justify-content-center">
            <div class="add p-2" @click="$emit('showProduct', index)">
              <PlusIcon class="plus-icon" />
              <span
                v-if="catinfo.hasProduct < 1"
                class="ml-1"
              >Add products to this category</span>
              <span v-else class="ml-1">View products of this category</span>
            </div>
          </div>
          <div class="mt-2 w-100 d-flex justify-content-center">
            <div class="d-flex flex-center btn btn-primary" @click="Preview()">
              <EyeIcon class="eye-icon" />
              <span class="ml-1">Preview</span>
            </div>
          </div>
          <div class="mt-4 px-5 py-4">
            <MobileToggle
              :title="toggles.card.title"
              :desc="toggles.card.desc"
              v-model="catinfo.isFeature"
              @change="addCategory"
            />
            <MobileToggle
              class="mt-4"
              :title="toggles.selection.title"
              :desc="toggles.selection.desc"
              v-model="catinfo.isSelection"
              @change="addCategory"
            />
          </div>
          <div class="py-2 px-5" v-show="catinfo.isSelection">
            <div class="d-flex flex-row justify-content-between">
              <div class="d-flex" style="width:50%;">
                <input
                  v-model="catinfo.selectionName"
                  type="text"
                  class="form-control"
                  placeholder="Enter selection name"
                  @blur="addCategory()"
                />
              </div>
              <div class="d-flex">
                <v-popover>
                  <div class="select mb-2" style="width: 100%">
                    <span
                      class="select-text mr-1"
                      v-if="catinfo.selectionType != ''"
                    >{{ catinfo.selectionType }}</span>
                    <span class="select-text mr-1" v-else>Select selection</span>
                    <span class="select-icon" style="float:right !important;">
                      <i class="las la-angle-down"></i>
                    </span>
                  </div>
                  <template slot="popover">
                    <ul class="list" v-close-popover>
                      <li @click="selectSelectionType('Collection')" class="list-item">Collection</li>
                      <li @click="selectSelectionType('Category')" class="list-item">Category</li>
                    </ul>
                  </template>
                </v-popover>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- <div class="d-flex flex-column" v-if="categoryList.length>1 && categoryList.length<10">
        <span
          class="circle"
          :class="{'circle-active':index==selectedCategory}"
          v-for="(value,index) in categoryList"
          :key="index"
        ></span>
      </div>-->
    </div>

    <div class="w-100 p-3">
      <div class="add px-3 py-2 d-flex align-items-center" @click="$emit('addCategory')">
        <StageTitle stageNum="C+" title="Add category" desc="click here to add category" />
      </div>
    </div>
    <div class="mt-2 w-100 d-flex flex-center">
      <ForwardButton @click.native="$emit('showProduct', selectedCategory)" />
    </div>
    <AlertBox v-if="!shopExist" :alertInfo="alertInfo" @goto="route('/account/contactinfo')" />
  </div>
</template>

<script>
import PreviewCardWithIphoneFrame from "@/components/PreviewCardWithIphoneFrame.vue";
import BackButton from "@/components/BackButton";
import StageTitle from "@/components/StageTitle";
import MobileToggle from "@/components/MobileToggle";
import AddedForm from "@/components/AddedForm";
import CloseIcon from "@/assets/icons/Close";
import EyeIcon from "@/assets/icons/Eye";
import PlusIcon from "@/assets/icons/Plus";
import UpArrowIcon from "@/assets/icons/UpArrow";
import DownArrowIcon from "@/assets/icons/DownArrow";
import ForwardButton from "@/components/ForwardButton";
import AlertBox from "@/components/AlertBox";
import axios from "axios";
import { debounce } from "lodash";

export default {
  components: {
    PreviewCardWithIphoneFrame,
    StageTitle,
    BackButton,
    AddedForm,
    MobileToggle,
    CloseIcon,
    EyeIcon,
    PlusIcon,
    UpArrowIcon,
    DownArrowIcon,
    ForwardButton,
    AlertBox
  },
  props: {
    categoryList: {
      type: Array
    }
  },
  data() {
    return {
      showPreview: false,
      photoUploading: false,
      previewCardDefaults: {
        title: "Your product category",
        desc: "Description for product information will be provided here",
        image:
          "https://portalvhdslvb28rs1c3tmc.blob.core.windows.net/yammo/allstaricon/picture.png"
       },
      stageInfoText: {
        title: "Define Category and Product",
        desc: "It makes customers know about your product"
      },
      selectedCategory: 0,
      info: {
        title: "Type category name (Eng)",
        desc: "Type category description here (Eng)",
        titleMyan: "Type category name (Myan)",
        descMyan: "Type category description here (Myan)",
        photoText: "add category photo",
        type: "Category"
      },
      toggles: {
        card: {
          title: "Feature as 'Card' in bot",
          desc: ""
        },
        selection: {
          title: "Feature this category as 'Selection'",
          desc: "Enable this category to show under 'Selection' section"
        }
      },
      alertInfo: {
        title: "Alert",
        msg:
          "To fill your product information,you need to add contact information first.",
        buttons: [
          //   {
          //     text: "OK",
          //     action: "ok"
          //   },
          {
            text: "Add Shop",
            action: "goto"
          }
          // {
          //   text: "Go Back",
          //   action: "back"
          // }
        ]
      }
    };
  },
  computed: {
    previewCardTitle() {
      return this.categoryList[this.selectedCategory].title
        ? this.categoryList[this.selectedCategory].title
        : this.previewCardDefaults.title;
    },
    previewCardDesc() {
      return this.categoryList[this.selectedCategory].desc
        ? this.categoryList[this.selectedCategory].desc
        : this.previewCardDefaults.desc;
    },
    previewCardTitleMyan() {
      return this.categoryList[this.selectedCategory].titleMyan
        ? this.categoryList[this.selectedCategory].titleMyan
        : this.previewCardDefaults.title;
    },
    previewCardDescMyan() {
      return this.categoryList[this.selectedCategory].descMyan
        ? this.categoryList[this.selectedCategory].descMyan
        : this.previewCardDefaults.desc;
    },
    previewCardImage() {
      return this.categoryList[this.selectedCategory].image
        ? this.categoryList[this.selectedCategory].image
        : this.previewCardDefaults.image;
    },
    shopExist() {
      return this.$store.state.shopID != "0";
    },
    shopID() {
      return this.$store.state.shopID;
    },
    customerID() {
      return this.$store.state.customerID;
    },
    isFindOther(){
      return this.categoryList[this.selectedCategory].isFindOther;
    },
    isContactUs(){
      return this.categoryList[this.selectedCategory].isContactUs;
    },
    shop(){
      return this.$store.state.shop;
    }
  },
  methods: {
    selectCategory(index) {
      this.selectedCategory = index;
    },
    selectSelectionType(type) {
      this.categoryList[
        this.selectedCategory
      ].selectionType = type;
      this.addCategory();
    },
    async uploadPhoto(url) {
      let guid = this.categoryList[this.selectedCategory].guid;
      let obj = {
        Guid: guid,
        ShopGuid: this.shopID,
        CustomerID: this.customerID,
        Photo: url,
        ShopID:this.shop.id,
        ShopName:this.shop.name
      };
      this.photoUploading = true;
      if (guid == "") {
        await axios.post("category/uploadphoto", obj).then(res => {
          this.categoryList[this.selectedCategory].guid = res.data;
          this.photoUploading = false;
        });
      } else {
        await axios.put("category/uploadphoto", obj).then(() => {
          this.photoUploading = false;
        });
      }
    },
    addCategory: debounce(async function() {
      let validate = this.validateForm();
      if (!validate) return;
      let guid = this.categoryList[this.selectedCategory].guid;
      let obj = {
        Guid: guid,
        ShopGuid: this.shopID,
        ShopID:this.shop.id,
        ShopName:this.shop.name,
        CustomerID: this.customerID,
        Name: this.categoryList[this.selectedCategory].title,
        Description: this.categoryList[this.selectedCategory].desc,
        IsFeatured: this.categoryList[this.selectedCategory]
          .isFeature,
        IsForSelection: this.categoryList[this.selectedCategory]
          .isSelection,
        SelectionName: this.categoryList[this.selectedCategory]
          .selectionName,
        SelectionType: this.categoryList[this.selectedCategory]
          .selectionType,
        IsFindOther: this.categoryList[this.selectedCategory]
          .isFindOther,
        IsContactUs: this.categoryList[this.selectedCategory]
          .isContactUs
      };
      if (guid == "") {
        await axios.post("category/upsert", obj).then(res => {
          this.categoryList[this.selectedCategory].guid = res.data;
        });
      } else {
        await axios.put("category/upsert", obj);
      }
    }, 2000),
    route(path) {
      this.$router.push(path);
    },
    validateForm() {
      if (this.shopID == "0" || this.shopID == "") return false;
      if (
        this.categoryList[this.selectedCategory].title == "" &&
        this.categoryList[this.selectedCategory].desc == ""
      ) {
        return false;
      }
      return true;
    },
    Preview() {
      this.$refs.iphone.scrollIntoView();
      this.showPreview = true;
    }
  }
};
</script>
<style lang="scss">
.iphoneframe-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: -1.5rem;
}
</style>
<style lang="scss" scoped>
@import "@/scss/_variables";
.frame {
  // max-width: 320px;
  position: relative;
}
.circle {
  min-width: 0.8rem;
  min-height: 0.8rem;
  margin: 10px 0;
  border-radius: 50%;
  background-color: $lightPurple;
  &-active {
    background-color: $primaryActive !important;
    min-width: 1rem;
    min-height: 1rem;
  }
}
.title {
  padding-left: 0.8rem;
}
.add {
  background-color: #dce9ff;
  border-radius: 10px;
}
.count {
  position: absolute;
  width: 2.5rem;
  height: 2.5rem;
  background-color: $bgWhite;
  border-radius: 50%;
  top: 7%;
  left: 12%;
  z-index: 1;
}
.menu {
  position: absolute;
  font-size: 1rem;
  color: $bgWhite;
  background-color: $primaryActive;
  border-radius: 50%;
  width: 1.2rem;
  height: 1.2rem;
  &--arrow {
    &-up {
      bottom: 1.9rem;
    }
    &-down {
      top: 1.9rem;
    }
  }
}
.btn-primary {
  border-radius: 40px;
  background-color: $lightBlue;
  border-color: unset;
  &:hover {
    border-color: unset;
    box-shadow: none;
  }
  &:active {
    border-color: unset;
    box-shadow: none;
  }
  &:focus {
    border-color: unset;
    box-shadow: none;
  }
}
.form-control {
  border: none;
  border-radius: 10px;
  background-color: $bgGray;
  font-size: 0.9rem !important;
  &:focus {
    box-shadow: none;
  }
  &-select {
    background-color: $lightPurple;
  }
}
.eye-icon {
  width: 0.9rem;
  height: auto;
  margin-top: -2px;
}
.plus-icon {
  width: 0.9rem;
  height: 0.9rem;
}
.arrow-icon {
  width: 0.7rem;
  height: 0.7rem;
}
.btn-circle {
  width: 40px;
  height: 40px;
  background: $grayActive;
}
.select {
  background: $lightPurple;
  padding: 0.375rem 0.55rem;
  border-radius: 10px;
  min-width: 9rem;
  max-width: 17rem;
  &-text {
    font-size: 0.9rem;
    color: $textColor;
  }
}
.list {
  list-style-type: none;
  //padding: .375rem .55rem;
  padding: 0;
  margin: 0;
  font-size: 0.9rem;
  color: $textColor;
  &-item {
    border-bottom: 1px solid #c1c1c1;
    padding: 0.375rem 0.55rem;
    cursor: pointer;
  }
  &-item:hover {
    background: $primaryActive;
    color: $bgWhite;
  }
}
li:last-child {
  border-bottom: 0.5px solid #c1c1c1;
}
li:first-child {
  border-top: 0.5px solid #c1c1c1;
}
</style>
