<template>
  <div class="d-flex flex-column vh-100">
    <div class="page py-4" style="overflow-x:hidden;">
      <category
        :categoryList="categoryList"
        @addCategory="addCategory"
        @showProduct="showProduct"
        v-show="showCategory"
      />
      <product
        v-if="!showCategory"
        :productList="productList"
        :categories="categories"
        :load="load"
        :categoryID="categoryList[selectedCategory].guid"
        @showCategory="showCategory = true"
        @createProduct="createProduct"
        @editProduct="editProduct"
        @deleteProduct="deleteProduct"
      />
    </div>
    <bottom-navigation-bar />
    <AlertBox v-if="showAlert" :alertInfo="alertInfo" @ok="showAlert=false" />
    <Loading v-if="showLoading" />
  </div>
</template>

<script>
import Product from "./ManageProduct/Product";
import Category from "./ManageProduct/Category";
import axios from "axios";
import BottomNavigationBar from "@/components/BottomNavigationBar";
import AlertBox from "@/components/AlertBox";
import Loading from "@/components/Loading";
export default {
  components: {
    Category,
    Product,
    BottomNavigationBar,
    AlertBox,
    Loading
  },
  data() {
    return {
      search: "",
      productList: [],
      categoryList: [
        {
            guid: "",
            title: "",
            desc: "",
            image: "",
            titleMyan: "",
            descMyan: "",
            hasProduct: false,
            isFeature: false,
            isSelection: false,
            selectionName: "",
            selectionType: "",
            isFindOther: false,
            isContactUs: false
          }
      ],
      showCategory: true,
      selectedCategory: 0,
      showAlert: false,
      alertInfo: {
        title: "Alert",
        msg:
          "To fill your product information,you need to add category first completely.",
        buttons: [
          {
            text: "OK",
            action: "ok"
          }
        ]
      },
      showLoading: true,
      load: false
    };
  },
  computed: {
    shopID() {
      return this.$store.state.shopID;
    },
    customerID() {
      return this.$store.state.customerID;
    },
    categories(){
      let list=this.categoryList.filter(x=>x.guid!="" && (x.title!="" && x.title!=null)).map(x=>{
        let obj={id:x.guid,name:x.title};
        return obj;
      });
      return list;
    }
  },
  mounted() {
    this.getCategoryList();
  },
  watch: {
    shopID() {
      this.getCategoryList();
    }
  },
  methods: {
    async getCategoryList() {
      if (this.shopID == "" || this.shopID == "0") return;
      var res = await axios.get(
        `categories?shopID=${this.shopID}&customerID=${this.customerID}`
      );
      if (res.status == 200 && res.data.length > 0) {
        this.categoryList = res.data;
      //  this.$store.commit("updateCategoryList", this.categoryList);
      }
      this.showLoading = false;
    },
    async getProductList(index) {
      this.productList = [];
      if (!this.categoryList[index].hasProduct) return;
        this.load=true;
      var res = await axios.get(
        `items?categoryID=${this.categoryList[index].guid}`
      );
      if (res.status == 200) {
        this.productList = res.data;
        this.load = false;
      }
    },
    addCategory() {
      let obj = {
          guid: "",
          title: "",
          desc: "",
          image: "",
          titleMyan: "",
          descMyan: "",
          hasProduct: 0,
          isFeature: false,
          isSelection: false,
          selectionName: "",
          selectionType: "",
          isFindOther: true,
          isContactUs: true
        
      };
      this.categoryList.push(obj);
    },
    showProduct(index) {
      //fetch getproduct api when it request to show
      if (
        this.categoryList[index].guid == "" ||
        this.categoryList[index].title == null ||
        this.categoryList[index].title == ""
      ) {
        this.showAlert = true;
        return;
      }
      this.showCategory = false;
      this.selectedCategory = index;
      this.getProductList(index);
    },
    createProduct(product) {
      this.productList.push(product);
      this.categoryList[this.selectedCategory].hasProduct = true;
    },
    editProduct(product) {
      let index = this.productList.findIndex(x => x.guid == product.guid);
      this.productList.splice(index, 1, product);
    },
    deleteProduct(product) {
      let index = this.productList.findIndex(x => x.guid == product.guid);
      this.productList.splice(index, 1);
      if (this.productList.length == 0) {
        this.categoryList[
          this.selectedCategory
        ].hasProduct = false;
      }
    }
  }
};
</script>
<style lang="scss">
@import "@/scss/_PageSetup.scss";
@import "@/scss/_Utilities.scss";
.page {
  flex: 1;
  overflow-y: auto;
}
</style>
