<template>
  <div class="d-flex w-100">
    <div class="imageBar overflow-auto" v-if="uploadImageList.length>0">
      <div class="imageBar--inside">
        <div class="imageBar--photo" v-for="(i,index) in uploadImageList" :key="index">
          <div class="imageBar--photo-bg" :style="{backgroundImage:'url('+url(i)+')'}"></div>
          <button class="imageBar--photo-close" @click="deleteUploadImage(index)">
            <!--<i class="las la-times-circle" />-->
            <CircleCloseIcon class="close-icon" />
          </button>
        </div>
      </div>
    </div>
    <div class="addPhoto addPhoto-text" :class="{'small':small}" @click="imageUpload">
      <PlusIcon class="plus-icon"/>
      <span style="font-size: 0.9rem; text-align: center;">{{ title }}</span>
      <input
        @change="onChange"
        type="file"
        accept="image/*"
        multiple
        style="display:none;"
        ref="fileInput"
      />
    </div>
  </div>
</template>

<script>
import PlusIcon from "@/assets/icons/Plus";
import CircleCloseIcon from "@/assets/icons/CircleClose";
export default {
  components: {
    PlusIcon,
    CircleCloseIcon
  },
  props: {
    title: {
      type: String
    },
    value: {
      type: String
    },
    small: {
      type: Boolean,
      default: false
    },
    type:{
      type:String
    }
  },
  data() {
    return {
      uploadImageList: []
    };
  },
  watch: {
    uploadImageList() {
      if (this.uploadImageList.length < 1) return;
      this.$emit("input", JSON.stringify(this.uploadImageList));
    },
    value() {
      if (this.value != "") {
        this.uploadImageList = JSON.parse(this.value);
        return;
      }
      this.uploadImageList=[];
    }
  },
  methods: {
    imageUpload() {
      this.$refs.fileInput.click();
    },
    onChange(e) {
      let fileList = e.target.files;
      for (let i = 0; i < fileList.length; i++) {
        let file = e.target.files[i];
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => {
          // setTimeout(() => {
          this.uploadImageList.push(reader.result);
          // }, 0);
        };
      }
      e.target.value = null; //remove last file from input to upload same file again
    },
    url(i) {
    //return i;
     if (i.includes("base64")) return i;
     return `https://kktstroage.azureedge.net/yammo/ecominya/` + i;
    },
    deleteUploadImage(index) {
      this.uploadImageList.splice(index, 1);
    }
  },
  mounted() {
    if (this.value != "") {
      this.uploadImageList = JSON.parse(this.value);
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/scss/_variables";

.small {
  min-width: 6rem !important;
  min-height: 6rem !important;
}
.addPhoto {
  height:6rem;
  width:6rem;
  background-color: $darkGray;
  color: $textColor;;
  font-weight: 300;
  border-radius: 10px;  
  &-text {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    flex-direction: column;
  }
}
.close {
  position: absolute;
  top: 8px;
  font-size: 20px;
  height: 20px;
  width: 20px;
  color: $grayActive;
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  transition: opacity 150ms ease-out;
  &-photo {
    right: 8px;
  }
}
.imageBar {
  display: flex;
  &--inside {
    overflow: auto;
    white-space: nowrap;
    //margin-left: 12px;
    margin-right: 12px;
  }
  &--photo {
    border-radius: 10px;
    height: 6rem;
    width: 6rem;
    display: inline-block;
    margin-right: 8px;
    overflow: hidden;
    position: relative;
    &:hover .imageBar--photo-close {
      opacity: 1;
      transition: none;
    }
    &:last-child {
      margin-right: 0px;
    }
    &-bg {
      background-position: 50% 50%;
      background-repeat: no-repeat;
      height: 7rem;
      width: 7rem;
      background-size: cover;
      transform: rotate(0deg);
    }
    &-close {
      height: 20px;
      position: absolute;
      right: 8px;
      top: 8px;
      font-size: 20px;
      width: 20px;
      color: $bgWhite;
      background-color: transparent;
      border: none;
      cursor: pointer;
      opacity: 0;
      padding: 0;
      transition: opacity 150ms ease-out;
    }
  }
}
.plus-icon{
  width: 1.1rem;
  height: 1.1rem;
  margin-bottom:5px;
}
.close-icon {
  width: 1.1rem;
  height: auto;  
}
</style>