<template>
  <div class="form-container">
    <div class="d-flex flex-column mb-4">
      <span class="title">Product Information</span>
      <span class="description">Register your product information</span>
    </div>

    <div class="d-flex flex-row justify-content-start align-items-center">
      <AddPhoto
        :small="true"
        :title="info.photoText"
        :uploading="uploading"
        type="item"
        v-model="value.coverPhoto"
        @uploadPhoto="uploadPhoto"
        class="mt-2"
      />
      <div class="d-flex flex-column mr-2 flex-fill">
        <input
          type="text"
          class="form-control input m-2"
          :placeholder="info.title"
          v-model="value.title"
        />
        <div class="d-flex m-2 w-100">
          <input
            type="text"
            class="form-control input input-button"
            :placeholder="info.code"
            disabled
            v-model="value.code"
          />
          <button class="btn generate btn-disabled" :disabled="disabled" @click="generate">Generate</button>
        </div>
      </div>
    </div>

    <div class="d-flex flex-row justify-content-between mt-2">
      <input
        type="text"
        class="form-control input mt-1"
        style="width:48%;"
        :placeholder="info.salesMMK"
        v-model="value.saleMMK"
      />
      <input
        type="text"
        class="form-control input mt-1"
        style="width:48%"
        :placeholder="info.salesUSD"
        v-model="value.saleUSD"
      />
    </div>
    <div class="d-flex flex-column mt-2">
      <multiselect
        v-model="value.categoryGuid"
        placeholder="Search Category"
        label="name"
        track-by="id"
        @remove="remove"
        :searchable="false"
        :options="categoryList"
        :multiple="true"
        :taggable="true"
        :show-labels="false"
      ></multiselect>
    </div>
    <div class="d-flex flex-column mt-2">
      <textarea
        type="text"
        class="form-control input mt-1 mb-2"
        placeholder="Fill more information"
        v-model="value.moreInfo"
        style="height:100px;padding-bottom:60px;"
      />
    </div>
    <div class="mt-2">
      <div class="d-flex justify-content-between">
        <span>Additional Information</span>
        <span v-if="showAdditional" @click="showAdditional=false">
          <ArrowUpIcon class="arrow" />
        </span>
        <span v-else @click="showAdditional=true">
          <ArrowDownIcon class="arrow" />
        </span>
      </div>
      <div v-if="showAdditional">
        <div class="d-flex flex-row justify-content-between mt-2">
          <input
            type="text"
            class="form-control input mt-1"
            style="width:48%;"
            :placeholder="info.purchasesMMK"
            v-model="value.purchasedMMK"
          />
          <input
            type="text"
            class="form-control input mt-1"
            style="width:48%"
            :placeholder="info.purchasesUSD"
            v-model="value.purchasedUSD"
          />
        </div>
        <textarea
          type="text"
          class="form-control input mt-3"
          placeholder="fill more information"
          v-model="value.moreInfoForAdd"
          style="height:100px;padding-bottom:60px;"
        />
      </div>
    </div>
    <div class="mt-2">
      <div class="d-flex flex-wrap mt-1 mb-2">
        <AddPhotoMultiple 
        type="item"
        :small="true" v-model="value.photos" :title="info.photosText" />
      </div>

      <MobileToggle
        :title="toggles.MoreInformation.title"
        :desc="toggles.MoreInformation.desc"
        v-model="value.isEnabledMoreInfo"
        class="mt-3"
      />
      <div class="d-flex justify-content-end mt-3">
        <button class="btn btn-primary mr-2"  v-if="value.guid==''">
          <span v-if="!loading" @click="$emit('create')">Create</span>
          <span v-else>Creating</span>
          <div
            v-if="loading"
            class="spinner-border text-light ml-2"
            style="width:1rem;height:1rem;"
            role="status"
          >
            <span class="sr-only">Loading...</span>
          </div>
        </button>
        <button v-else class="btn btn-primary mr-2" >
          <span v-if="!loading" @click="$emit('edit')">Edit</span>
          <span v-else>Editing</span>
          <div
            v-if="loading"
            class="spinner-border text-light ml-2"
            style="width:1rem;height:1rem;"
            role="status"
          >
            <span class="sr-only">Loading...</span>
          </div>
        </button>
        <button class="btn btn-cancel" style="border-radius:20px;" @click="$emit('cancel')">Cancel</button>
      </div>
    </div>
    <!-- <div class="p-2 d-flex flex-center">
      <span class="m-2" @click="isEnglish=true" :class="{'lang-active':isEnglish}">Eng</span>
      <span class="m-2" @click="isEnglish=false" :class="{'lang-active':!isEnglish}">Myan</span>
    </div>-->
  </div>
</template>
<script>
import AddPhoto from "@/components/AddPhoto";
import AddPhotoMultiple from "@/components/AddPhotoMultiple";
import MobileToggle from "@/components/MobileToggle";
import ArrowUpIcon from "@/assets/icons/ArrowUp";
import ArrowDownIcon from "@/assets/icons/ArrowDown";

export default {
  props: {
    value: {
      type: Object,
      required: true
    },
    info: {
      type: Object
    },
    loading: {
      type: Boolean
    },
    categoryID: {
      type: String
    },
    categoryList: {
      type: Array,
      required: true
    }
  },
  components: {
    MobileToggle,
    AddPhoto,
    AddPhotoMultiple,
    ArrowUpIcon,
    ArrowDownIcon
  },
  data() {
    return {
      contactNo: "",
      fbPageUrl: "",
      moreInfo: "",
      showAdditional: false,
      isEnglish: true,
      toggles: {
        MoreInformation: {
          title: "Enable 'More information'",
          desc:
            "Enable this option will show 'More information button' inside product card and provide more information of product"
        }
      },
      uploading: false
    };
  },
  watch: {
    value() {
      this.$emit("input", this.value);
    }
  },
  computed:{
    disabled(){
      return this.value.code?true:false;
    }
  },
  methods: {
    remove(removedOption){
      if(removedOption.id==this.categoryID){
        this.value.categoryGuid.unshift(removedOption);
      }
    },
    uploadPhoto() {
      this.uploading = true;
      setTimeout(() => {
        this.uploading = false;
      }, 3000);
    },
    generate(){
      let uuid="";
      for(var i=0;i<5;i++)
      {
        uuid+=Math.floor(Math.random()*10);
      }
      this.value.code=uuid;
    }
  },
 
};
</script>
<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style lang="scss">
@import "@/scss/_variables";
.multiselect__element {
  font-size: 0.9rem;
}
.multiselect__input {
  font-size: 0.9rem;
}
.multiselect__tags {
  min-height: 30px;
  display: block;
  padding: 8px 40px 0 8px;
  border-radius: 10px;
  border: none !important;
  background: $bgWhite;
  font-size: 0.8rem;
}
.multiselect__tag {
  position: relative;
  display: inline-block;
  padding: 7px 30px 7px 10px;
  border-radius: 15px;
  margin-right: 10px;
  color: #575757;
  line-height: 1;
  background: #dce9ff;
  margin-bottom: 5px;
  white-space: nowrap;
  overflow: hidden;
  max-width: 100%;
  text-overflow: ellipsis;
}
.multiselect__tag-icon {
  cursor: pointer;
  margin-left: 7px;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  font-weight: 500;
  font-style: normal;
  width: 20px;
  height: 20px;
  text-align: center;
  line-height: 22px;
  transition: all 0.2s ease;
  font-size: 1rem;
  color: $bgWhite;
  background: $textColor;
  border-radius: 50%;
  margin-top: 3.2px;
  margin-right: 5px;
}
.multiselect__tag-icon:hover {
    background: $textColor;
}
.multiselect__tag-icon:after {
  content: "\D7";
  color: $bgWhite;
  background: $textColor;
  font-size: 1rem;
}
.multiselect__placeholder {
  color: $textColor !important;
  display: inline-block;
  margin-bottom: 10px;
  padding-top: 2px;
  font-size: 0.9rem !important;
}
.multiselect__option {
  display: block;
  padding: 12px;
  min-height: 40px;
  line-height: 16px;
  text-decoration: none;
  text-transform: none;
  vertical-align: middle;
  position: relative;
  cursor: pointer;
  white-space: nowrap;
}
.multiselect__option--highlight {
  background: $lightPurple;
  outline: none;
  color: $textColor;
}
.multiselect__option--selected.multiselect__option--highlight {
  background: $lightPurple;
  color: $textColor;
}
</style>
<style lang="scss" scoped>
@import "@/scss/_variables";
.form-container {
  display: flex;
  flex-direction: column;
  padding: 10px;
  background-color: $bgGray;
  border-radius: 10px;
}

.icon-form {
  display: block;
  height: calc(1.5em + 0.75rem + 2px);
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: $bgWhite;
  background-clip: padding-box;
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
}
.title {
  font-size: 1rem;
  font-weight: 700;
}
.description {
  font-size: 0.7rem;
}

.form-control {
  &:focus {
    box-shadow: none;
  }
}
.input {
  border: none;
  border-radius: 10px;
  font-size: 0.9rem !important;
}
.input-button {
  border-bottom-right-radius: 0px;
  border: none;
  border-top-right-radius: 0px;
}
.btn-cancel {
  background-color: $grayActive;
  color: $bgWhite;
  font-size: 0.9rem;
  border-radius: 20px;
}
.btn-primary {
  font-size: 0.9rem;
  border-radius: 20px;
}
.arrow {
  width: 1.5rem;
  height: 1.5rem;
}
.generate {
  background-color: $lightPurple;
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  font-size: 0.9rem;
}
.lang-active {
  color: $primaryActive;
  font-size: 1.3rem;
}
.plus-icon {
  width: 1.1rem !important;
  height: 1.1rem !important;
  margin-bottom: 5px;
}
</style>
