<template>
  <div class="iphoneframe-container" ref="iphone">
    <BackButton @click="$emit('showCategory')" />
    <transition name="fade-top">
      <PreviewCardWithIphoneFrame
        class="w-100"
        :image="previewCardImage"
        :title="previewCardTitle"
        :desc="previewCardDesc"
        :isFindOther="true"
        :isContactUs="true"
        type="item"
        firstButtonText="More information"
        secondButtonText="Order now"
        thirdButtonText="Contact us"
        v-show="showPreview"
      />
    </transition>

    <div class="mt-4 w-100 d-flex justify-content-center" v-if="showPreview">
      <button class="btn btn-circle" @click="showPreview = false">
        <CloseIcon style="width: 0.8rem;" />
      </button>
    </div>

    <div class="mt-4 w-100 d-flex title" ref="title">
      <StageTitle stageNum="2" :title="stageInfoText.title" :desc="stageInfoText.desc" />
    </div>

    <div
      class="mt-4 w-100 d-flex justify-content-between align-items-center p-3"
      v-if="productList.length>0"
    >
      <div class="d-flex align-items-center">
        <span class="head-icon">
          <SearchIcon class="icon" />
        </span>
        <input type="text" placeholder="Search Product" v-model="search" class="form-control" />
      </div>
      <!--<div class="head">
        <span class="head-icon">
          <i class="las la-filter"></i>
        </span>
      </div>-->
    </div>

    <div class="w-100 d-flex flex-column px-3 py-1" v-if="productList.length>0">
      <ProductList
        v-for="p in list"
        :key="p.guid"
        :product="p"
        @openPreview="openPreview"
        @edit="edit"
        @deleteProduct="deleteProduct"
      />
    </div>

    <div :class="productList.length>0 ? 'w-100 frame p-3' : 'mt-4 w-100 frame p-3'">
      <ProductForm
        v-model="productInfo"
        :info="info"
        :loading="loading"
        :categoryList="categories"
        :categoryID="categoryID"
        @create="manageProduct"
        @edit="manageProduct"
        @cancel="cancelProduct"
      />
    </div>
    <div class="p-3 w-100">
      <div class="add px-3 py-2 d-flex align-items-center" @click="cancelProduct">
        <StageTitle stageNum="P+" title="Add product" desc="click here to add product" />
      </div>
    </div>
    <div class="mt-2 w-100 d-flex flex-center">
      <router-link to="/account/thankyouinfo">
        <ForwardButton @click.native="$emit('showPage')" />
      </router-link>
    </div>
    <Loading v-if="showLoading || load" />
  </div>
</template>

<script>
import PreviewCardWithIphoneFrame from "@/components/PreviewCardWithIphoneFrame.vue";
import BackButton from "@/components/BackButton";
import StageTitle from "@/components/StageTitle";
import ProductList from "./ProductList";
import ProductForm from "./ProductForm";
import ForwardButton from "@/components/ForwardButton";
import SearchIcon from "@/assets/icons/Search";
import axios from "axios";
import CloseIcon from "@/assets/icons/Close";
import Loading from "@/components/Loading";

export default {
  components: {
    PreviewCardWithIphoneFrame,
    StageTitle,
    BackButton,
    ProductList,
    ProductForm,
    ForwardButton,
    SearchIcon,
    CloseIcon,
    Loading
  },
  props: {
    productList: {
      type: Array,
      required: true
    },
    categoryID: {
      type: String
    },
    categories:{
      type:Array
    },
    load: {
      type: Boolean
    }
  },
  data() {
    return {
      showPreview: false,
      search: "",
      loading: false,
      showLoading: false,
      previewCardDefaults: {
        title: "Your product category",
        desc: "Description for product information will be provided here",
        image:
          "https://portalvhdslvb28rs1c3tmc.blob.core.windows.net/yammo/allstaricon/picture.png"
      },
      info: {
        title: "Type product name",
        code: "Code",
        salesMMK: "Sales Price MMK",
        salesUSD: "Sales Price USD",
        purchasesMMK: "Purchases Price MMK",
        purchasesUSD: "Purchases Price USD",
        photoText: "add cover photo",
        photosText: "add photos"
      },
      productInfo: {
        guid: "",
        title: "",
        code: 0,
        saleMMK: "",
        saleUSD: "",
        coverPhoto: "",
        photos: "",
        moreInfo: "",
        moreInfoForAdd: "",
        purchasedMMK: "",
        purchasedUSD: "",
        categoryGuid: [],
        isEnabledMoreInfo: false
      },
      productPreview: {
        title: "",
        desc: "",
        image: ""
      }
    };
  },
  computed: {
    previewCardTitle() {
      return this.productPreview.title
        ? this.productPreview.title
        : this.previewCardDefaults.title;
    },
    previewCardDesc() {
      return this.productPreview.desc
        ? this.productPreview.desc
        : this.previewCardDefaults.desc;
    },
    previewCardImage() {
      return this.productPreview.image
        ? this.productPreview.image
        : this.previewCardDefaults.image;
    },
    shopID() {
      return this.$store.state.shopID;
    },
    categoryList() {
      return this.$store.state.categoryList;
    },
    customerID() {
      return this.$store.state.customerID;
    },
    list() {
      if (this.search == "") return this.productList;
      else {
        return this.productList.filter(
          x =>
            x.title.toLowerCase().startsWith(this.search.toLowerCase()) ||
            x.title.toLowerCase().includes(" " + this.search.toLowerCase())
        );
      }
    },
    stageInfoText(){
      let category=this.categories.findIndex(x=>x.id==this.categoryID);
      return {
        title:"Products for '" + this.categories[category].name + "'",
        desc:"Register product for '"+this.categories[category].name+"'"
      };
    },
    defaultCategory(){
      let index=this.categories.findIndex(x=>x.id==this.categoryID);
      let array=[this.categories[index]];
      return array;
    },
    shop(){
      return this.$store.state.shop;
    }
  },
  methods: {
    cancelProduct() {
      this.productInfo = {
        guid: "",
        title: "",
        code: 0,
        saleMMK: "",
        saleUSD: "",
        coverPhoto: "",
        photos: "",
        moreInfo: "",
        moreInfoForAdd: "",
        purchasedMMK: "",
        purchasedUSD: "",
        categoryGuid: this.defaultCategory,
        isEnabledMoreInfo: false
      };
    },
    cancelPreview() {
      this.productPreview = {
        title: "",
        MMK: "",
        image: ""
      };
    },
    hidePreview() {
      this.showPreview = false;
    },
    async manageProduct() {
      this.loading = true;
      let obj = {
        guid: this.productInfo.guid,
        shopID:this.shop.id,
        shopName:this.shop.name,
        title: this.productInfo.title,
        photos: this.productInfo.photos,
        coverPhoto: this.productInfo.coverPhoto,
        saleMMK: this.productInfo.saleMMK,
        saleUSD: this.productInfo.saleUSD,
        purchasedMMK: this.productInfo.purchasedMMK,
        purchasedUSD: this.productInfo.purchasedUSD,
        moreInfoForAdd: this.productInfo.moreInfoForAdd,
        moreInfo: this.productInfo.moreInfo,
        code: this.productInfo.code,
        shopGuid: this.shopID,
        categoryGuid: JSON.stringify(this.productInfo.categoryGuid),
        isEnabledMoreInfo: this.productInfo.isEnabledMoreInfo
      };
      if (this.productInfo.guid == "") {
        await axios.post("item/upsert", obj).then(res => {
          this.productInfo.guid = res.data;
          this.$emit("createProduct", obj);
          this.cancelProduct();
          this.loading = false;
        });
      } else {
        await axios.put("item/upsert", obj).then(() => {
          this.$emit("editProduct", obj);
          this.cancelProduct();
          this.loading = false;
        });
      }
    },
    openPreview(product) {
      this.productPreview.title = product.title;
      this.productPreview.desc = product.moreInfo;
      this.productPreview.image = product.coverPhoto;
      this.showPreview = true;
      this.$refs.iphone.scrollIntoView();
    },
    edit(product) {
      this.productInfo.guid = product.guid;
      this.productInfo.title = product.title;
      this.productInfo.code = product.code;
      this.productInfo.saleMMK = product.saleMMK;
      this.productInfo.saleUSD = product.saleUSD;
      this.productInfo.coverPhoto = product.coverPhoto;
      this.productInfo.photos = product.photos;
      this.productInfo.moreInfo = product.moreInfo;
      this.productInfo.moreInfoForAdd = product.moreInfoForAdd;
      this.productInfo.purchasedMMK = product.purchasedMMK;
      this.productInfo.purchasedUSD = product.purchasedUSD;
      this.productInfo.isEnabledMoreInfo = product.isEnabledMoreInfo;
      this.productInfo.categoryGuid = JSON.parse(product.categoryGuid);
    },
    async deleteProduct(product) {
      this.showLoading = true;
      var res = await axios.delete(`item/delete?guid=${product.guid}`);
      if (res.status == 200) {
        this.showLoading = false;
        if (product.guid == this.productInfo.guid) {
          this.cancelProduct();
        }
        if (product.guid == this.productPreview.guid) {
          this.hidePreview();
        }
        this.$emit("deleteProduct", product);
      }
    },
  },
  mounted() {
    this.$refs.iphone.scrollIntoView();
    this.productInfo.categoryGuid=[...this.defaultCategory];
  }
};
</script>
<style lang="scss">
.iphoneframe-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: -1.5rem;
}
</style>
<style lang="scss" scoped>
@import "@/scss/_variables";
.frame {
  position: relative;
}
.title {
  padding-left: 0.8rem;
}
.add {
  background-color: $lightPurple;
  border-radius: 10px;
}
.count {
  position: absolute;
  width: 2rem;
  height: 2rem;
  background-color: $bgWhite;
  border-radius: 50%;
  top: 2%;
  left: 12%;
}
.head {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  transition: 0.3s;
  background-color: $bgGray;
  padding-top: 4px;
}
.head-icon {
  font-size: 1.4rem;
  color: $textColor;
  cursor: pointer;
}
.form-control {
  font-size: 0.9rem;
  border: none;
  &:focus {
    box-shadow: none;
  }
}
.icon {
  width: 1.2rem;
  height: 1.2rem;
  cursor: pointer;
}
.btn-circle {
  width: 40px;
  height: 40px;
  background: $grayActive;
}
</style>
