<template>
  <div>
    <div class="d-flex justify-content-between align-items-center user mb-4">
      <div class="d-flex flex-row align-items-center flex-fill" @click="show">
        <div class="circle-image" :style="{backgroundImage:'url('+url(product.coverPhoto)+')'}"></div>

        <div class="d-flex flex-column ml-2">
          <span class="user-name">{{product.title}}</span>
          <span class="user-user">{{product.saleMMK}}</span>
        </div>
        <transition name="fade">
          <div class="d-flex flex-fill justify-content-end" v-if="!showMenu" >
            <div class="drag d-flex flex-center" @click.stop="show">
              <span class="drag-item">
                <GridLinesVerticalIcon class="icon"/>
              </span>
            </div>
          </div>
        </transition>
      </div>
      <transition name="fade-right">
        <div v-if="showMenu" style="width:60%;" class="d-flex">
          <div class="d-flex flex-center close" @click.stop="show">
            <span class="product-item">
              <CircleCloseIcon class="icon"/>
            </span>
          </div>
          <div class="d-flex align-items-center flex-fill justify-content-between product">            
            <span class="product-item">
              <ShareIcon class="icon"/>
            </span>
            <span class="product-item" @click="$emit('openPreview',product)">
              <EyeNotFillIcon  class="icon"/>
            </span>
            <span class="product-item" @click="$emit('edit',product)">
              <EditIcon class="icon"/>
            </span>
            <span class="product-item" @click="$emit('deleteProduct',product)">
              <TrashIcon class="icon"/>
            </span>
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import GridLinesVerticalIcon from "@/assets/icons/GridLinesVertical";
import EditIcon from "@/assets/icons/Edit";
import ShareIcon from "@/assets/icons/Share";
import TrashIcon from "@/assets/icons/Trash";
import CircleCloseIcon from "@/assets/icons/CircleClose";
import EyeNotFillIcon from "@/assets/icons/EyeNotFill";
import Eventbus from "@/eventbus.js";
export default {
  components:{
    GridLinesVerticalIcon,
    EyeNotFillIcon,
    EditIcon,
    ShareIcon,
    TrashIcon,
    CircleCloseIcon
  },
  props: ["product"],
  methods: {
    url(i) {
      return i;
   //   if (i.includes("base64")) return i;
     // return "https://kktstroage.azureedge.net/yammo/ecommerce/item/" + i;
    },
    show() {
      if (!this.showMenu) {
        this.showMenu = true;
        Eventbus.$emit("showMenu", this.product.guid);
        return;
      }
      this.showMenu=false;
    }
  },
  data() {
    return {
      showMenu: false
    };
  },
  mounted() {
    Eventbus.$on("showMenu", guid => {
      if (guid != this.product.guid) {
        this.showMenu = false;
      }
    });
  }
};
</script>

<style lang="scss" scoped>
@import "@/scss/_variables.scss";  
.user {
  cursor: pointer;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  &-name {
    font-size: 1rem;
    font-weight: bold;
  }
  &-user {
    font-size: 0.8rem;
  }
}

.circle-image {
  border-radius: 50%;
  min-width: 3rem;
  min-height: 3rem;
  background-size: cover;
  background-position: center;
}
.product {
  background-color: $lightPurple;
  margin-right: -1rem;
  padding: 0 1rem;
  &-item {
    background: none;
    font-size: 1.3rem;
    padding: 0.5rem 0;
  }
}
.close {
  background-color: $grayActive;
  border-top-left-radius: 1rem;
  border-bottom-left-radius: 1rem;
  padding: 0 1rem;
}
.drag {
  background-color: $grayActive;
  border-top-left-radius: 1rem;
  border-bottom-left-radius: 1rem;
  margin-right: -1rem;
  padding: 0 1rem;
  opacity: 0.5;
  &-item {
    font-size: 1.3rem;
    padding: 0.5rem 0;
  }
}
.icon{
  width:1rem;
  height:1rem;
  cursor: pointer;
}
</style>